import { NgModule } from '@angular/core';
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './web/main/main.component';
import { ClassInfoComponent } from './web/class-info/class-info.component';
import { ClassGroupListComponent } from './web/class-group-list/class-group-list.component';
import { MyGroupsComponent } from './web/my-groups/my-groups.component';
import { CreateGroupComponent } from './web/create-group/create-group.component';
import { EditGroupComponent } from './web/edit-group/edit-group.component';
import { SearchClassComponent } from './web/search-class/search-class.component';
import { InterestsGroupsComponent } from './web/interests-groups/interests-groups.component';
import { SelectGroupComponent } from './web/select-group/select-group.component';
import { CreateInterestGroupComponent } from './web/create-interest-group/create-interest-group.component';
import { CreateInterestGroup2Component } from './web/create-interest-group2/create-interest-group2.component';
import { EditInterestGroupComponent } from './web/edit-interest-group/edit-interest-group.component';
import { GroupDetailComponent } from './web/group-detail/group-detail.component';
import { SelectSchoolComponent } from './web/select-school/select-school.component';
import { MySchoolComponent } from './web/my-school/my-school.component';
import { SearchInterestGroupComponent } from './web/search-interest-group/search-interest-group.component';
import { AddCourseComponent } from './web/add-course/add-course.component';
import { ConnectGroupCourseComponent } from './web/connect-group-course/connect-group-course.component';
const childRoutes: Routes = [
    { path: '', component: InterestsGroupsComponent, data: { animation: 'main' } },//搜索页
    { path: 'search-class', component: SearchClassComponent, data: { animation: 'main' } },//搜索页
    { path: 'search-school', component: SelectSchoolComponent, data: { animation: 'main' } },//搜索页
    { path: 'interests-groups', component: InterestsGroupsComponent, data: { animation: 'interests' } },//喜欢的列表
    { path: 'my-groups-list', component: MyGroupsComponent, data: { animation: 'my-groups' } },// 我的群组
]
// 定义常量 路由
const appRoutes: Routes = [
    { path: '', component: MainComponent, children: childRoutes, data: { animation: 'index' } },
    // {path: '',component: MainComponent},
    { path: 'class-info', component: ClassInfoComponent, data: { animation: 'class-info' } },//课程详情
    { path: 'class-group-list', component: ClassGroupListComponent, data: { animation: 'class-group-list' } },//课程群组列表
    { path: 'group-detail', component: GroupDetailComponent, data: { animation: 'group-detail' } },//课程群组列表
    { path: 'create-group', component: CreateGroupComponent, data: { animation: 'create-group' } },//创建群组
    { path: 'edit-group', component: EditGroupComponent, data: { animation: 'edit-group' } },//编辑群组
    { path: 'create-interest-group', component: CreateInterestGroup2Component, data: { animation: 'create-interest-group' } },//创建Interest群组
    { path: 'edit-interest-group', component: EditInterestGroupComponent, data: { animation: 'edit-interest-group' } },//编辑Interest群组
    { path: 'my-school', component: MySchoolComponent},//用户所属学校
    { path: 'search-interest', component: SearchInterestGroupComponent, data: { animation: 'my-school' } },//interest groups 查询
    { path: 'add-course', component: AddCourseComponent, data: { animation: 'my-school' } },//新增课程
    { path: 'connect-group-course', component: ConnectGroupCourseComponent, data: { animation: 'my-school' } },//新增课程后添加群
    {
        path: '**',
        redirectTo: '/',
        pathMatch: 'full'
    }
    // { path: 'login', component: LoginComponent },

];

export const Routing: ModuleWithProviders = RouterModule.forRoot(appRoutes, { useHash: true });
